import React from "react";

function RecipeDetailTitle({ data }) {
  return (
    <div className="recipedetail-title">
      <div className="recipedetail-title__img">
        <img src={data.ATT_FILE_NO_MAIN} alt="" />
      </div>
      <div className="recipedetail-title__content">
        <h1>{data.RCP_NM}</h1>
        <div>
          <span>{data.INFO_ENG}</span>
          <span>{data.INFO_CAR}</span>
          <span>{data.INFO_PRO}</span>
          <span>{data.INFO_FAT}</span>
          <span>{data.INFO_NA}</span>
        </div>
      </div>
    </div>
  );
}

export default RecipeDetailTitle;
